import React, { Component, Suspense } from 'react';
const EmbedCode = React.lazy(() => import('../../../PublishCommonComponents/EmbedCode'));
class WixEmbed extends Component {

    render() {
        return  <div className="card text-decoration-none">
                    <div className="card-body p-lg-7">
                        <div className="m-auto">
                        <div className="embed_code">
                            <h5 className="mb-5 fw-bolder">Embed Widget On Wix Website</h5>
                            <h6>1. Adjust the width and height of the widget as per your requirement</h6>
                            <Suspense fallback={''}>
                                <EmbedCode copyText={'2. Copy the generated embed code for your Wix Website'} />
                            </Suspense>
                            <h6 className="mb-5">3. Paste the publish code into the backend of the page</h6>
                            <ul className="list-unstyled list-points fs-7">
                                <li className="mb-3">Log in to your Wix account</li>
                                <li className="mb-3">Select and edit the page where you want to embed the widget and apply changes to display it on your website</li>
                                <li className="mb-3">Click on (+) to add new block and choose Embed Code and select Embed HTML option</li>
                                <li>Paste your copied code and click on Update to complete the process!</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
    }
}

export default WixEmbed;